import { AddAdminComponent } from './pages/admin-panel/add-admin/add-admin.component';
import {LoginService} from './pages/login/login.service';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    UpperlogoComponent,
    HomeComponent,
    MembershipComponent,
    NewsEventsComponent,
    ContactComponent,
    AdminPanelComponent,
    LoginComponent,
    UsersComponent,
    TableComponent,
    FooterComponent,
    SponsorstripComponent,
    EditHomePageComponent,
    AddAdminComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AngularFireDatabaseModule, // imports firebase/database, only needed for database features
    AngularFireAuthModule, // imports firebase/auth, only needed for auth features
    AngularFireModule.initializeApp(environment.firebase),
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyDWkkWYyJt5nb0mURORr4HEFcX_ZPox12g'
    }),
    routing
  ],
  //HttpService, AuthGaurd, PermGuard
  providers: [UserService, HttpService, LoginService],
  bootstrap: [AppComponent]
})
export class AppModule { }





